<template>
    <div v-show="show" class="row justify-content-center">
        <div v-show="show" class="card card-inverse" style="width: 30rem; background-color: #333; border-color: #333;">
            <img class="card-img-top" :src='movies.Poster' alt="img.png">
            <div class="card-block">
                <h4 class="card-title">{{movies.Title}}</h4>
                <p class="card-text">{{movies.Plot}}</p>
            </div>
            <div>
                <ul class="list-group-item-text">
                    <!-- <icon name="imdb" scale="10"></icon> -->
                </ul>
                <ul class="list-group">
                    <!-- <icon name="imdb"></icon> -->
                    <li class="list-group-item list-group-item-info" v-for="rating in movies.Ratings" :key="rating"><span v-if="rating.Source == 'Internet Movie Database'" class="label label-default label-pill"><icon name="imdb" color="black" scale="3" style="margin-right: .5rem"></icon></span> {{rating.Source}}: {{rating.Value}}</li>
                </ul>
            </div>
            <div class="btn text-left" @click="show=!show" style="padding:0; margin: .25rem .25rem 0 .25rem; width: 4rem; cursor: pointer;">
                <icon name='times-circle' color="red" scale="1.5"></icon>
            </div>
        </div>
    </div>
</template>
<script>
import Icon from 'vue-awesome/components/Icon.vue'
import { EventBus } from '../bus.js'
export default {
    components: {
        Icon
    },
    name: 'Result',
    data() {
        return {
            movies: {},
            baseUrl: 'https://www.omdbapi.com/?apikey=98cae6de&t=',
            // home: {},
            search: '',
            // imgUrl: 'https://image.tmdb.org/t/p/original',
            title: "",
            show: false
        }
    },

    created() {
        this.$http.get('https://api.themoviedb.org/3/movie/now_playing?api_key=aebb1ec70575ef6ccfab6d37e09f205d&language=en-US&page=1')
            .then(response => {
                this.home = response.body.results
            }, response => {});
    },

    methods: {
        result() {
            this.$http.get(this.baseUrl + this.search)
                .then(response => {
                    this.movies = response.body
                    this.show = !this.show
                }, response => {
                    // error callback
                });
        }
    },
    mounted () {
        EventBus.$on('update', movies => {
            this.movies = movies
            this.show = true
        })
    }
};
</script>
<style lang="css" scoped>
</style>



